
import { defineComponent } from "vue";
import { valueCodeList, COM21100113_RES, standardCodeDetailLanguageList, COM21100132_RES } from "@/shared/type/com211001sc02.type";
import { modalController } from "@ionic/vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";

export default defineComponent({
    name: "com211001sc02",
    components: {
        BreadCrumb
    },

    data() {
        const commonCodeValue = [] as valueCodeList[];
        const statusCode = [
            { key: '01', value: 'Active' },
            { key: '02', value: 'Inactive' },
        ]
        const form = {
            standardCodeDomainName: this.$route.query.standardCodeDomainName,
            standardCodeDomainNo: this.$route.query.standardCodeDomainNo,
            businessCommonStatusCode: "01",
            standardCodeDetailLanguageList: [
                {
                    languageCode: "En",
                    standardCodeValueDesc: "",
                    businessCommonStatusCode: "01",
                },
            ] as standardCodeDetailLanguageList[],
            standardCodeValue: "",
            standardCodeDisplayOrder: 0,
        } as valueCodeList
        const isEditable = true as boolean
        const isInValidInput = false as boolean
        const isCodeValueDuplicated = false as boolean
        const isDescriptionDuplicated = false as boolean
        const isInValidAdd = false as boolean
        const dataLength = '' as string

        return {
            isEditable,
            form,
            commonCodeValue,
            isInValidInput,
            isInValidAdd,
            isCodeValueDuplicated,
            isDescriptionDuplicated,
            dataLength,
            statusCode,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        };
    },

    updated() {
        this.isCodeValueDuplicated = this.handleDuplicateCodeValue(this.commonCodeValue);
        this.isDescriptionDuplicated = this.handleDuplicateDescription(this.commonCodeValue);
    },

    async mounted() {
        if (this.approvalType === '') {
            this.requestCommonCodeValue();
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.commonCodeValue = authDetailParsed.list;
                this.commonCodeValue = this.commonCodeValue.map((item: valueCodeList) => {

                    return {
                        businessCommonStatusCode: item.businessCommonStatusCode,
                        standardCodeDisplayOrder: item.standardCodeDisplayOrder,
                        standardCodeDomainName: item.standardCodeDomainName,
                        standardCodeDomainNo: item.standardCodeDomainNo,
                        standardCodeValue: item.standardCodeValue,
                        standardCodeDetailLanguageList: item.standardCodeDetailLanguageList.length !== 0 ? item.standardCodeDetailLanguageList : [{
                            languageCode: "En",
                            businessCommonStatusCode: "01",
                            standardCodeValueDesc: ""
                        }]
                    }
                })
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        covertCodeValue() {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        }
    },

    methods: {
        async requestCommonCodeValue() {
            const reqBody = {
                standardCodeDomainName: this.$route.query.standardCodeDomainName,
            };
            const res = (await this.$request.getRequest(
                "COM21100112",
                reqBody,
                false
            )) as COM21100113_RES; //adapter from commonCodeDetail
            if (res.header.result) {
                this.commonCodeValue = res.body.valueCodeList;
                for (let i = 0; i < res.body.valueCodeList.length; i++) {
                    this.commonCodeValue[i].standardCodeDetailLanguageList = res.body.valueCodeList[i].standardCodeDetailLanguageList.filter(item => item.languageCode == "En")
                }
            }

            this.commonCodeValue = this.commonCodeValue.map((item: valueCodeList) => {

                return {
                    businessCommonStatusCode: item.businessCommonStatusCode,
                    standardCodeDisplayOrder: item.standardCodeDisplayOrder,
                    standardCodeDomainName: item.standardCodeDomainName,
                    standardCodeDomainNo: item.standardCodeDomainNo,
                    standardCodeValue: item.standardCodeValue,
                    standardCodeDetailLanguageList: item.standardCodeDetailLanguageList.length !== 0 ? item.standardCodeDetailLanguageList : [{
                        languageCode: "En",
                        businessCommonStatusCode: "01",
                        standardCodeValueDesc: ""
                    }]
                }
            })
            this.dataLength = res.body.codeDataLengthContent;
        },

        async handleEdit() {
            this.commonCodeValue = this.commonCodeValue.map((item: valueCodeList, index) => {
                return {
                    businessCommonStatusCode: item.businessCommonStatusCode,
                    standardCodeDisplayOrder: index + 1,
                    standardCodeDomainName: item.standardCodeDomainName,
                    standardCodeDomainNo: item.standardCodeDomainNo,
                    standardCodeValue: item.standardCodeValue,
                    standardCodeDetailLanguageList: item.standardCodeDetailLanguageList
                }
            });
            const reqBody = { list: this.commonCodeValue };

            const res = await this.$request.getRequest("COM21100132", reqBody, false) as COM21100132_RES;
            if (res.header.result) {
                this.isEditable = true;
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestCommonCodeValue()
                        modalController.dismiss()
                    }
                });
            }
        },
        
        handleReorder(event: CustomEvent) {
            this.commonCodeValue = event.detail.complete(this.commonCodeValue);
        },

        handleActionClick(actionType: string) {
            switch (actionType) {
                case "edit":
                    this.isInValidAdd = false;
                    this.isEditable = false;
                    break;
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$t("message.cancel_without_saving_changes")}`,
                        dialogMode: "Question",
                        btnLabel: this.$t("common.confirm"),
                        callback: () => {
                            this.requestCommonCodeValue();
                            this.isEditable = true;
                            this.isInValidAdd = false;
                            this.isInValidInput = false
                        },
                    });
                    break;
                case "add":
                    if (
                        this.form.standardCodeValue &&
                        this.form.standardCodeDetailLanguageList[0].languageCode &&
                        this.form.standardCodeDetailLanguageList[0].standardCodeValueDesc &&
                        this.form.standardCodeDetailLanguageList[0].businessCommonStatusCode &&
                        this.isCodeValueDuplicated && this.isDescriptionDuplicated
                    ) {
                        this.form.standardCodeDisplayOrder = this.commonCodeValue.length + 1;
                        this.commonCodeValue.push(JSON.parse(JSON.stringify(this.form)))
                        this.form.standardCodeValue = ''
                        this.form.standardCodeDetailLanguageList[0].standardCodeValueDesc = ''
                        this.isInValidAdd = false;
                        this.$util.scrollToElement('inp_error')
                    } else if (
                        !this.form.standardCodeValue ||
                        !this.form.standardCodeDetailLanguageList[0].languageCode ||
                        !this.form.standardCodeDetailLanguageList[0].standardCodeValueDesc ||
                        !this.form.standardCodeDetailLanguageList[0].businessCommonStatusCode
                    ) {
                        this.isInValidAdd = true;
                        this.$util.scrollToElement('inp_error')
                    } else if (!this.isCodeValueDuplicated || !this.isDescriptionDuplicated) {
                        this.isInValidAdd = true;
                        this.$util.scrollToElement('inp_error')
                    }
                    break;
                case "save":
                    var validation = this.handleCommonCodeValidation(this.commonCodeValue);
                    var standardCodeDetailLanguageListValidation = this.handleCodeValueValidation(this.commonCodeValue);
                    if (validation && standardCodeDetailLanguageListValidation) {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                this.handleEdit();
                                this.isEditable = true;
                                this.isInValidInput = false;
                            },
                        });
                    } else if (!validation || !standardCodeDetailLanguageListValidation) {
                        this.isInValidInput = true;
                        this.$util.scrollToElement('inp_error')
                    }
                    break;
                case 'list':
                    this.$router.push("COM211001SC00");
                    break;
            }
        },

        handleCommonCodeValidation(item: valueCodeList[]): boolean {
            let isEmty = true;
            for (let i = 0; i < item.length; i++) {
                if (item[i].standardCodeValue !== "") {
                    item[i];
                } else {
                    isEmty = false;
                }
            }
            return isEmty;
        },

        handleCodeValueValidation(item: valueCodeList[]) {
            let isEmty = true;
            for (let i = 0; i < item.length; i++) {
                for (let j = 0; j < item[i].standardCodeDetailLanguageList.length; j++) {
                    if (
                        item[i].standardCodeDetailLanguageList[j].businessCommonStatusCode &&
                        item[i].standardCodeDetailLanguageList[j].languageCode &&
                        item[i].standardCodeDetailLanguageList[j].standardCodeValueDesc
                    ) {
                        item[i].standardCodeDetailLanguageList[j];
                    } else {
                        isEmty = false;
                    }
                }
            }
            return isEmty;
        },

        handleDuplicateCodeValue(item: valueCodeList[]) {
            let isEmty = true;
            for (let i = 0; i < item.length; i++) {
                if (item[i].standardCodeValue !== this.form.standardCodeValue) {
                    //true
                } else {
                    isEmty = false;
                }
            }
            return isEmty;
        },

        handleDuplicateDescription(item: valueCodeList[]) {
            let isEmty = true;
            for (let i = 0; i < item.length; i++) {
                for (let j = 0; j < item[i].standardCodeDetailLanguageList.length; j++) {
                    if (
                        item[i].standardCodeDetailLanguageList[j].standardCodeValueDesc !== this.form.standardCodeDetailLanguageList[0].standardCodeValueDesc
                    ) {
                        //true
                    } else {
                        isEmty = false;
                    }
                }
            }
            return isEmty;
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        },
    },
});
